.main-menu{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  &::after{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .7);
    opacity: 0;
    transition-property: opacity;
    transition-duration: 500ms;
    z-index: 99;
  }
}

.main-menu__list{
  list-style: none;
  margin: 0;
  padding-left: 32px;
  padding-right: 32px;
  padding-top: 15px;
  padding-bottom: 0;
  position: relative;
  background: url('../img/girl-mobile.png') right top no-repeat $primary;
  z-index: 200;
  max-width: 320px;
  box-sizing: border-box;
  height: 100%;
  transform: translateX(-100%);
  transition-duration: 500ms;
  transition-property: transform;
  overflow-y: auto;
}

.main-menu__item{
  margin: 20px 0;
}

.main-menu__link, .main-menu__close{
  font-size: px-to-rem(14px);
  line-height: px-to-rem(16px);
  text-decoration: none;
  color: $black;
  border: none;
  cursor: pointer;
  &:hover{
    text-decoration: underline;
  }
}

.main-menu__close{
  padding-left: 30px;
  background: url('../img/close.svg') left center no-repeat;
}

.main-menu__phone{
  font-size: px-to-rem(24px);
  line-height: px-to-rem(28px);
  text-decoration: none;
  color: $black;
}

.main-menu__btn{
  @include btn{
    color: $black;
    background-color: $white;
  }
}

.main-menu__callback{
  opacity: 0.5;
  font-size: px-to-rem(14px);
  line-height: px-to-rem(16px);
  margin-top: 25px;
  margin-bottom: 0;
}

.main-menu--active{
  z-index: 200;
  &::after{
    content: "";
    opacity: 1;
  }
  .main-menu__list{
    transform: translateX(0);
  }
}

@media (min-width: 1024px) and (min-height: 768px){
  .main-menu__list{
    background: url('../img/girl.png') center bottom no-repeat $primary;
    padding-left: 64px;
    padding-top: 64px;
  }
  .main-menu__btn, .main-menu__callback, .main-menu__phone{
    display: none;
  }
}